.section.-map {
  @media #{$mobile} {
    background: #F8F8F8;
  }
}

.favorite-places-link {
  position: absolute;
  left: 48px;
  top: 48px;

  @media #{$mobile} {
    display: none;
  }

  > .icon {
    display: inline-block;
    vertical-align: middle;
    width: 24px;
    height: 24px;

    svg {
      width: 100%;
      height: 100%;

      .icon-heart {
        stroke: $black;
        fill: $black;
        transition: all .4s ease;
      }
    }
  }

  > .text {
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
    opacity: 0.54;
    font-size: 12px;
    color: #272727;
    letter-spacing: 0px;
    line-height: 16px;
  }

  &:hover {
    > .icon {
      svg {
        .icon-heart {
          fill: $red;
          stroke: $red;
        }
      }
    }
  }
}

.map-moscow-screen {
  height: 100%;
  height: 100vh;
}

.map-content {
  position: relative;
  height: 100%;
  box-sizing: border-box;
  padding-top: 120px;
  margin: 0 50% 0 164px;

  @media #{$mobile} {
    position: static;
    margin: 0;
    height: auto;
    padding: 0;
  }
}

.map-mobile-warning {
  position: absolute;
  top: 160px;
  left: 0;
  right: 0;
  opacity: 0.54;
  font-size: 12px;
  letter-spacing: 0;
  line-height: 16px;
  text-align: center;
}

.map-mobile-fake {
  position: absolute;
  top: 150px;
  bottom: 0;
  left: 0;
  right: 0;
  background: url(../images/map-fake.svg) center / contain no-repeat;
}

.categories-main-list {
  position: absolute !important;
  box-sizing: border-box;
  padding-bottom: 24px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  pointer-events: none;
  transition: all .4s ease;

  &.isActive {
    opacity: 1;
    pointer-events: all;
  }

  .category-description {
    margin-top: 24px;

    &:first-child {
      margin-top: 0;
    }
  }

  .mCSB_scrollTools {
    opacity: 0 !important;
    pointer-events: none;
  }
}

.category-preview-container {
  position: relative;
  box-sizing: border-box;
  margin-top: calc(30vh - 125px);

  @media #{$mobile} {
    // display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin-top: 0 !important;

    .categories-main-list,
    .category-examples {
      display: none;
    }

    .category-description {
      .category-card {
        > .title {
          min-height: 46px;
        }
      }
    }
  }

  @media screen and (max-height: 53rem) {
    margin-top: 70px;
  }

  .mCSB_1_scrollbar {
    opacity: 0 !important;
    pointer-events: none;
  }
}

.category-description {
  position: relative;
  padding: 0 80px 0 115px;
  overflow: hidden;
  transition: all .4s ease;

  @media #{$mobile} {
    z-index: 2;
    padding: 0 24px;
    background: #fff;
    border-bottom: 1px solid #E0E2E4;
    transition: none;
  }

  .category-card {
    > .title {
      @media #{$mobile} {
        font-size: 20px;
        line-height: 21px;
        padding-left: 72px;
        padding-top: 4px;
      }
    }

    > .desc {
      @media #{$mobile} {
        font-size: 15px;
        color: #272727;
        letter-spacing: 0;
        line-height: 20px;
      }
    }
  }

  &.isHidden {
    opacity: 0;
    pointer-events: none;
  }

  &.-link {
    cursor: pointer;

    .category-card {
      > .title {
        transition: color .4s ease;
      }

      > .desc {
        position: relative;

        > .count {
          position: absolute;
          top: 0;
          left: 0;
          opacity: 0;
        }

        > .text,
        > .count {
          // transition: opacity .4s ease;
          // backface-visibility: hidden;
        }
      }
    }

    &:hover {
      .category-card {
        > .title {
          color: $red;
        }

        > .desc {
          > .text {
            opacity: 0;
          }

          > .count {
            opacity: 1;
          }
        }
      }
    }
  }

  .left-side {
    position: absolute;
    top: 0;
    left: 20px;
    width: 48px;

    .icon {
      width: 48px;
      height: 48px;
      background: {
        size: 100%;
        repeat: no-repeat;
        position: center;
      }

      &.-winter {
        background-image: url(../images/categories/winter.svg);
      }
    }

    .text {
      margin-top: 14px;
      opacity: 0.54;
      font-size: 12px;
      color: #272727;
      letter-spacing: 0px;
      line-height: 16px;
      text-align: center;

      @media #{$mobile} {
        display: none;
      }
    }
  }
}

.mobile-header {
  position: fixed;
  z-index: 8;
  top: 0;
  left: 0;
  right: 0;
  padding: 7px 48px 26px;
  background: #fff;
  height: auto;
  border-bottom: 1px solid #E0E2E4;
  opacity: 0;
  pointer-events: none;
  transition: opacity .4s ease;

  &.isActive {
    opacity: 1;
    pointer-events: all;
    z-index: 9;
  }

  &.-no-border {
    border-bottom: none;
  }

  &.-without-desc {
    padding-top: 0;
    padding-bottom: 0;
    height: 56px;
    line-height: 56px;

    > .title {
      line-height: 56px;
    }
  }

  > .title {
    font-family: 'PT Mono', monospace;
    font-weight: 700;
    color: #222629;
    letter-spacing: 0px;
    font-size: 20px;
    line-height: 40px;
    position: relative;
  }

  > .desc {
    color: #272727;
    letter-spacing: 0px;
    font-size: 13px;
    line-height: 18px;
    margin-top: 0;
  }
}

.map-area-title {
  > .title {
    font-family: 'PT Mono', monospace;
    font-weight: 700;
    font-size: 64px;
    color: #222629;
    letter-spacing: 0px;
    line-height: 56px;
  }

  > .desc {
    font-size: 20px;
    color: #272727;
    letter-spacing: 0px;
    line-height: 24px;
    margin-top: 16px;

    @media #{$mobile} {
      font-size: 13px;
      line-height: 18px;
      margin-top: 0;
    }
  }
}

.mobile-prev-arrow {
  display: none;
  position: absolute;
  right: 100%;
  padding-right: 16px;
  top: 2px;

  svg {
    width: 9px;
  }
}

.mobile-menu-burger {
  position: absolute;
  z-index: 2;
  top: 16px;
  right: 16px;
  width: 24px;
  height: 24px;
  background: url(../images/icon_burger.svg) center / 80% no-repeat;

  @media #{$desktop} {
    display: none;
  }
}

.category-card {
  > .title {
    font-weight: 700;
    font-size: 32px;
    color: #272727;
    letter-spacing: 0px;
    line-height: 30px;
  }

  > .desc {
    font-size: 14px;
    color: #272727;
    letter-spacing: 0px;
    line-height: 21px;
    margin-top: 16px;
  }

  &.-medium {
    > .title {
      font-size: 24px;
      line-height: 28px;
    }

    > .desc {
      opacity: 0.82;
      font-size: 14px;
      line-height: 21px;
      margin-top: 14px;
    }
  }

  &.-xs {
    > .title {
      font-size: 24px;
      line-height: 28px;
    }

    > .desc {
      opacity: 0.54;
      font-size: 12px;
      line-height: 16px;
      margin-top: 4px;
    }
  }

  &.-inList {
    > .title {
      font-size: 16px;
      line-height: 24px;
      margin-top: 2px;
    }

    > .desc {
      opacity: 0.54;
      font-size: 12px;
      line-height: 16px;
      margin-top: 4px;
    }
  }
}

.category-examples {
  margin-top: 45px;
  overflow: hidden;

  > .category-example {
    width: 40%;
    float: left;

    &:last-child {
      margin-left: 20%;
    }
  }
}

.category-example {
  display: block;
  text-decoration: none;
  color: inherit;

  > .title {
    display: block;
    font-weight: 700;
    font-size: 16px;
    color: #272727;
    letter-spacing: 0.57px;
    line-height: 18px;
    transition: color .4s ease;

    &:first-letter {
      text-transform: uppercase;
    }
  }

  > .desc {
    display: block;
    opacity: 0.54;
    font-size: 12px;
    color: #272727;
    letter-spacing: 0px;
    line-height: 16px;
    margin-top: 11px;
  }

  &:hover {
    .title {
      color: #EC1D23;
    }
  }
}

.choose-category-link {
  position: absolute;
  z-index: 2;
  display: inline-block;
  bottom: 71px;
  left: 0;
  font-weight: 700;
  font-size: 16px;
  width: 149px;
  letter-spacing: 0.57px;
  text-decoration: none;
  line-height: 32px;
  color: inherit;
  border-bottom: 3px solid $red;
  transition: width .4s ease;
  white-space: nowrap;

  @media #{$mobile} {
    display: none;
  }

  > a {
    display: inline-block;
    transition: all .4s ease;
    text-decoration: none;
    color: inherit;

    &:hover {
      color: $red;
    }
  }

  > .close {
    position: absolute;
    left: 0;
    opacity: 0;
    pointer-events: none;
  }

  > .all {
    position: absolute;
    right: 0;
    opacity: 0;
    pointer-events: none;
  }

  &.isActive {
    width: 100%;

    .choose {
      opacity: 0;
    }

    .close,
    .all {
      opacity: 1;
      pointer-events: all;
    }
  }
}

.map-moscow-wrap {
  position: absolute;
  top: 75px;
  bottom: 75px;
  left: 50%;
  right: 13%;

  @media #{$mobile} {
    top: 200px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;

    * {
      -webkit-touch-callout: none !important; 
    }
  }
}

.map-moscow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 490px;
  height: 552px;
  transform: translate(-50%, -50%);

  @media #{$mobile} {
    width: 277px;
    height: 310px;
    font-size: 9px;
  }

  &:before {
    content: "";
    position: absolute;
    top: -10%;
    left: -20%;
    width: 140%;
    height: 140%;
    background: url(../images/moscow_shadow.png) center / contain no-repeat;
  }

  > .etalon {
    z-index: 1;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    margin-left: -4.125em;
    margin-top: -1.1875em;
    width: 39.4375em;
    height: 41.6875em;
  }

  .regions {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;

    .label {
      position: absolute;
      z-index: 2;
      pointer-events: none;
      transform: translate(-50%, -50%);
      font-weight: 700;
      font-size: 12px;
      letter-spacing: 0;
      transition: opacity .4s ease;

      @media #{$mobile} {
        display: none;
      }

      &.-capacity {
        opacity: 0;
        color: #fff;
      }

      &.isHovered {
        &.-capacity {
          opacity: 1;
        }

        &.-name {
          opacity: 0;
        }
      }

      &.-CAO {
        left: 56%;
        top: 48%;
      }

      &.-SAO {
        top: 30%;
        left: 43.5%;
      }

      &.-SVAO {
        top: 27%;
        left: 57.1%;
      }

      &.-SZAO {
        top: 38%;
        left: 32.7%;
      }

      &.-UAO {
        top: 75%;
        left: 59.7%;
      }

      &.-UVAO {
        top: 60%;
        left: 75.5%;
      }

      &.-UZAO {
        top: 68%;
        left: 47.1%;
      }

      &.-VAO {
        top: 38%;
        left: 73%;
      }

      &.-ZAO {
        top: 57.4%;
        left: 37%;
      }
    }

    .region {
      position: absolute;
      z-index: 1;
        
      path {
        fill: #fff;
        stroke: rgba(236, 29, 35, .3);
        transition: all .4s ease;
      }

      &.isHovered {
        path {
          fill: $red;
          stroke: $red;
        }
      }

      &.-region-CAO {
        width: 7.873125em;
        height: 6.0125em;
        left: 12.908125em;
        top: 13.6325em; 
      }
      &.-region-SAO {
        width: 9.651875em;
