      }
      &.-region-SVAO {
        width: 8.325625em;
        height: 11.954375em;
        left: 13.171875em;
        top: 2.50625em; 
      }
      &.-region-SZAO {
        width: 6.99125em;
        height: 11.78875em;
        left: 6.123125em;
        top: 5.76875em; 
      }
      &.-region-UAO {
        width: 8.699375em;
        height: 11.274375em;
        left: 15.1275em;
        top: 18.401875em; 
      }
      &.-region-UVAO {
        width: 7.901875em;
        height: 10.330625em;
        left: 18.821875em;
        top: 15.190625em; 
      }
      &.-region-UZAO {
        width: 5.97375em;
        height: 15.03875em;
