.map-region-screen {
  display: none;
  position: absolute;
  z-index: 101;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: #F8F8F8;
  opacity: 0;
  transition: opacity .4s ease;

  &.isActive {
    opacity: 1;
  }

  @media #{$mobile} {
    position: fixed;
    bottom: 0;
    -webkit-overflow-scrolling: touch;
  }
}

.map-left-block {
  position: absolute;
  width: 396px;
  height: 100%;
  left: 0;
  background: #fff;
  border-right: 2px solid $black;
  overflow: hidden;

  @media #{$mobile} {
    width: 100%;
    border: none;
    top: 172px;
    height: auto;
    bottom: 0;
    
    .inner-container {
      &.-places {
        transition: all .6s ease;
        transform: translateX(-100%);
      }
    }

    &.isActive {
      .inner-container {
        &.-places {
          transform: translateX(0);
        }
      }
    }
  }

  .inner-container {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    transition: all .6s ease;

    &.-place,
    &.-categories {
      transform: translateX(-100%);

      &.isActive {
        box-shadow: 0 0 15px rgba(0, 0, 0, .3);
        transform: translateX(0);
      }
    }

    &.-place {
      @media #{$mobile} {
        position: fixed;
        z-index: 10;

        .container {
          padding: 0 24px;
        }
      }
    }

    &.-categories {
      padding: 72px 0 42px;

      @media #{$mobile} {
        display: none;
      }
      
      .top-nav {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
      }
    }
  }

  &.-favorite {
    z-index: 102;
    transform: translateX(-100%);
    transition: all .6s ease;

    @media #{$mobile} {
      z-index: 103;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    &.isActive {
      transform: translateX(0);
    }
  }

  .container {
    margin: 0 48px;

    @media #{$mobile} {
      margin: 0 auto;
    }
  }

  .bottom-nav {
    border-top: 3px solid $red;
    padding: 8px 0;
    text-align: center;

    a {
      font-weight: 700;
      font-size: 14px;
      color: #222629;
      letter-spacing: 0.49px;
      text-decoration: none;
      @include redHover();
    }
  }

  .top-nav {
    margin-top: 40px;
    border-bottom: 3px solid $red;

    .favorites-open {
      display: inline-block;
      width: 24px;
      height: 24px;

      svg {
        path {
          transition: all .4s ease;
        }
      }

      &:hover {
        svg {
          path {
            fill: $red;
          }
        }        
      }
    }

    .right-link {
      float: right;
      font-weight: 700;
      font-size: 14px;
      color: #222629;
      letter-spacing: 0.49px;
      text-decoration: none;
      @include redHover();
    }
  }

  .category-info {
    overflow: hidden;
    border-bottom: 3px solid $red;

    @media #{$mobile} {
      display: none;
    }

    .mCSB_scrollTools {
      right: 0;
      opacity: 0 !important;
      pointer-events: none;
    }

    .category-description {
      padding: 36px 0 25px 68px;

      .left-side {
        left: 0;
        top: 25px;
      }
    }
  }

  .back-link {
    height: 72px;
    line-height: 72px;

    @media #{$mobile} {
      height: 56px;
      line-height: 56px;
    }

    a {
      font-weight: 700;
      font-size: 14px;
      color: #222629;
      letter-spacing: 0.49px;
      text-decoration: none;
      @include redHover();

      svg {
        display: inline-block;
        vertical-align: middle;
        height: 18px;
        width: 10px;
        margin-right: 24px;

        polygon {
          transition: all .4s ease;
        }
      }

      span {
        display: inline-block;
        vertical-align: middle;
      }

      &:hover {
        svg {
          polygon {
            fill: $red;
          }
        }
      }
    }
  }

  .place-image {
    height: 168px;
    background: {
      size: cover;
      position: center;
    }
  }

  .place-info {
    position: absolute;
    top: 72px;
    bottom: 110px;
    left: 0;
    right: 0;
    overflow: hidden;

    @media #{$mobile} {
      top: 56px;
      bottom: 64px;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      height: 58px;
      left: 0;
      right: 0;
      background: linear-gradient(to bottom, rgba(255, 255, 255, .0), rgba(255, 255, 255, 1));
    }

    .mCSB_scrollTools {
      right: 0;
      opacity: 0 !important;
      pointer-events: none;
    }

    .header {
      text-align: center;
      padding: 24px 0 0;

      @media #{$mobile} {
        padding-top: 16px;
      }

      .tag {
        opacity: 0.54;
        font-size: 12px;
        letter-spacing: 0;
        line-height: 16px;
      }

      .title {
        margin-top: 8px;
        font-weight: 700;
        font-size: 21px;
        letter-spacing: 0;
        line-height: 26px;

        @media #{$mobile} {
          font-size: 16px;
          letter-spacing: 0;
          line-height: 20px;
        }

        &:first-letter {
          text-transform: uppercase;
        }
      }

      &:after {
        content: "";
        display: block;
        margin: 24px auto 0;
        height: 3px;
        width: 48px;
        background: $red;

        @media #{$mobile} {
          margin-top: 16px;
        }
      }
    }

    .content {
      padding: 16px 0 58px;

      .info {
        margin-top: 16px;

        .title {
          opacity: 0.54;
          font-size: 12px;
          letter-spacing: 0;
          line-height: 16px;
        }

        .value {
          font-weight: 700;
          font-size: 14px;
          color: #272727;
          letter-spacing: 0;
          line-height: 16px;
        }
      }

      .description {
        opacity: 0.82;
        font-size: 14px;
        letter-spacing: 0;
        line-height: 24px;
        margin-top: 24px;

        a {
          color: inherit;
          text-decoration: none;
        }
      }
    }
  }

  .place-nav {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
    padding-bottom: 49px;
    line-height: 40px;

    @media #{$mobile} {
      padding-bottom: 16px;
    }

    .nav-icon {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      margin-left: 24px;

      @media #{$mobile} {
        margin-left: 16px;
      }

      &:first-child {
        margin-left: 0;
      }

      .inner-link {
        svg {
          width: 24px;

          path {
            transition: all .4s ease;
          }
        }
      }

      &.-like {
        .inner-link {
          svg {
            path {
              stroke: $black;
              fill: #fff;
            }
          }

          &:hover,
          &.isActive {
            svg {
              path {
                stroke: $red;
              }
            }
          }

          &.isActive {
            svg {
              path {
                fill: $red;
              }
            }
          }
        }
      }

      &.-share {
        .inner-link {
          svg {
            path {
              fill: $black;
            }
          }

          &:hover,
          &.isActive {
            svg {
              path {
                fill: $red;
              }
            }
          }
        }
      }
    }

    .nav-btn {
      display: inline-block;
      vertical-align: middle;
      background: $red;
      border-radius: 2px;
      height: 40px;
      line-height: 40px;
      padding: 0 24px;
      font-weight: 700;
      font-size: 16px;
      color: #FFFFFF;
      letter-spacing: 0.57px;
      text-decoration: none;
      float: right;
