@keyframes region-map {
  from {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(0, 2px);
  }
  20% {
    transform: translate(0, 3px);
  }
  30% {
    transform: translate(0, 2px);
  }
  40% {
    transform: translate(0, 1px);
  }
  50% {
    transform: translate(0, -1px);
  }
  60% {
    transform: translate(0, -2px);
  }
  70% {
    transform: translate(0, -3px);
  }
  80% {
    transform: translate(0, -2px);
  }
  90% {
    transform: translate(0, 0px);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes ballon {
  from {
    transform: translate(0, 0);
  }
  60% {
    transform: translate(0, 20%) scale(.95);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes cloud1 {
  from {
    transform: translate(0, 0);
  }
  40% {
    transform: translate(-30%, 0);
  }
  to {
    transform: translate(0, 0);
  }
}

@keyframes cloud2 {
  from {
    transform: translate(0, 0);
  }
  60% {
    transform: translate(35%, 0) scale(.95);
  }
  to {
    transform: translate(0, 0);
  }
}

.region-map {
  display: none;
  position: absolute;
  z-index: 1;
  animation: region-map 5s infinite linear;

  .image {
    display: block;
    position: relative;
    z-index: 1;
  }

  .shadow {
    position: absolute;
    top: 0;
    left: 0;
  }

  map {
    area {
      cursor: default;
    }
  }

  &.isActive {
    display: block;
  }

  &.isTransition {
    transition: all .4s ease;

    .image {
      transition: height .4s ease;
    }
  }
}

@mixin whiteScrollGradient() {
  &:before,
  &:after {
    content: "";
    position: absolute;
    height: 24px;
    left: 0;
    right: 0;
    pointer-events: none;
  }

  &:before {
    z-index: 1;
    top: 0;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, .0));
  }

  &:after {
    bottom: 0;
    background: linear-gradient(to bottom, rgba(255, 255, 255, .0), rgba(255, 255, 255, 1));
  }
}

