        padding-left: 68px;

        &:before {
          content: counter(places);
          counter-increment: places;
          position: absolute;
          top: 35px;
          left: 0;
          font-family: 'PT Mono', monospace;
          font-weight: 700;
          font-size: 40px;
          color: #EEEEEE;
          letter-spacing: 0;
          line-height: 36px;
        }

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7),
        &:nth-child(8),
        &:nth-child(9) {
          &:before {
            content: '0' counter(places);
          }
        }

        &.-empty {
          &:before {
            content: '00';
          }
        }
      }
    }
