    top: auto !important;
    left: 0 !important;
    right: 0;
    width: auto;
    bottom: 0;
    transform: translate(0, 0);
    padding-top: 24px;
  }

  .logo {
    width: 75px;
    height: 11px;
    background: url(../images/simple-logo.svg) center / contain no-repeat;
    margin: 0 auto;

    @media #{$mobile} {
      width: auto;
      height: 26px;
      background: {
        image: url(../images/simple-logo.svg), url(../images/icon_flag.png);
        size: 75px auto, 41px auto;
        repeat: no-repeat;
        position: 59px center, left center;
      }
    }
  }

  .title {
    margin-top: 16px;
    font-weight: 700;
    font-size: 16px;
    color: #272727;
    letter-spacing: 0.57px;
    line-height: 18px;

    @media #{$mobile} {
      margin-top: 24px;
    }
  }

  .desc {
    margin-top: 9px;
    opacity: 0.82;
    font-style: italic;
    font-size: 14px;
    color: #272727;
    letter-spacing: 0;
    line-height: 20px;
  }

  .footer {
    margin-top: 10px;

    a {
      text-decoration: none;
      color: #D6D6D6;
      font-size: 12px;
      letter-spacing: 0.11px;
      line-height: 14px;
      @include redHover();
    }
  }

  &:after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -6px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 6px 0 6px;
    border-color: #fff transparent transparent transparent;
  }

  &.isUpSide {
    transform: translate(-50%, 0);
    margin-top: 5%;

    &:after {
      display: none;
    }

    @media #{$mobile} {
      transform: translate(0, 0);
      margin: 0;
    }
  }
}

.map-place-tooltip {
  display: none;
  position: absolute;
  z-index: 999;
  padding: 0 24px;
  height: 45px;
  line-height: 45px;
  background: #fff;
  border-top: 3px solid $red;
  box-shadow: 0 15px 12px 0 rgba(0,0,0,0.07), inset 0 1px 1px 0 rgba(255,255,255,0.24);
  transform: translate(-50%, -100%);
  pointer-events: none;
  white-space: nowrap;

  &:after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -6px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 6px 0 6px;
    border-color: #fff transparent transparent transparent;
  }

  .text {
    font-weight: 700;
    font-size: 16px;
    color: #272727;
    letter-spacing: 0.57px;
    line-height: 18px;
  }
}

.popup-like {
  display: none;
  position: absolute;
  left: 1px;
  bottom: 140%;
  background: #FFFFFF;
  padding: 12px 24px;
  box-shadow: 0 15px 12px 0 rgba(0,0,0,0.07), inset 0 1px 1px 0 rgba(255,255,255,0.24);
  white-space: nowrap;
  border-top: 3px solid $red;

  .link {
    display: block;
    font-size: 12px;
    color: #D6D6D6;
    letter-spacing: 0.11px;
    line-height: 14px;
    text-decoration: none;
    @include redHover();
  }
}

.popup-share {
  display: none;
  position: absolute;
  left: 50%;
  margin-left: -65px;
  bottom: 140%;
  background: #FFFFFF;
  padding: 16px;
  box-shadow: 0 15px 12px 0 rgba(0,0,0,0.07), inset 0 1px 1px 0 rgba(255,255,255,0.24);

  &:after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -6px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 6px 0 6px;
    border-color: #fff transparent transparent transparent;
  }

  .link {
    display: block;
    width: 98px;
    height: 24px;
    margin: 8px auto 0;
    background: {
      size: contain;
      repeat: no-repeat;
      position: center;
    }

    &:first-child {
      margin-top: 0;
    }

    &.-tw {
      background-image: url(../images/share-tw.svg);
    }

    &.-fb {
      background-image: url(../images/share-fb.svg);
    }

    &.-vk {
      background-image: url(../images/share-vk.svg);
    }
  }
}

.mobile-region-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;

  .region-title {
    position: relative;
    height: 56px;
    line-height: 56px;
    text-align: center;
    border-bottom: 1px solid #E0E2E4;
    box-sizing: border-box;

    .text {
      font-weight: 700;
      font-size: 24px;
      color: #222629;
      letter-spacing: 0;
      text-transform: uppercase;

      .arrow-down {
        display: inline-block;
        vertical-align: middle;
        margin-left: 14px;
        transition: all .4s ease;

        svg {
          width: 12px;
        }
      }
    }

    .title-item {
      &.isActive {
        .text {
          .arrow-down {
            transform: rotate(180deg);
          }
        }
      }
    }

    .close-link {
      position: absolute;
      display: inline-block;
      top: 16px;
      right: 16px;
      width: 24px;
      height: 24px;
      background: url(../images/bold-cross.svg) center / 80% no-repeat;
    }

    .title-select-list {
      display: none;
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      z-index: 3;
      background: #fff;
      text-align: center;
      margin-top: 1px;

      .title-item {
        border-bottom: 1px solid #E0E2E4;
        height: 48px;
        line-height: 48px;

        .text {
          font-size: 20px;
        }
      }
    }
  }

  .tabs {
    border-bottom: 1px solid #E0E2E4;
    box-sizing: border-box;
    overflow: hidden;
    box-sizing: border-box;

    .tab {
      display: inline-block;
      float: left;
      width: 50%;
      text-align: center;

      > span {
        display: inline-block;
        width: 100px;
        height: 48px;
        line-height: 48px;
        opacity: 0.35;
        font-weight: 700;
        font-size: 16px;
        color: #222629;
        letter-spacing: 0.57px;
        transition: all .4s ease;
        border-bottom: 3px solid transparent;
      }

      &.isActive {
        > span {
          opacity: 1;
          border-bottom: 3px solid $red;
        }
      }
    }
  }
}

.category-select {
  height: 64px;
  background: #fff;
  border-bottom: 1px solid #E0E2E4;
  box-sizing: border-box;

  .category-description {
    border-bottom: none;
    min-height: 48px;
    padding: 8px 16px 8px;
    box-sizing: border-box;
    height: 100%;

    > .left-side {
      top: 8px;
    }

    > .right-side {
      padding-right: 24px;
    }

    .category-card {
      > .title {
        font-weight: 700;
        font-size: 15px;
        color: #222629;
        letter-spacing: 0.53px;
        line-height: 18px;
        padding-left: 64px;
        margin-top: 3px;
        width: 125px;
        min-height: 45px;
      }
    }

    .select-arrow {
      position: absolute;
      width: 24px;
      height: 24px;
      right: 16px;
      top: 50%;
      margin-top: -12px;
      background: url(../images/icon-arrow_down.svg) center / 50% no-repeat;
      transition: all .4s ease;
    }

    &.isActive {
      .select-arrow {
        transform: rotate(180deg);
      }
    }
  }
}

.mobile-menu {
  position: fixed;
  z-index: 102;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  box-sizing: border-box;
  padding: 0 24px;
  text-align: center;

  .favorite-places-link {
    display: block;
    top: 16px;
    left: 16px;
  }

  .mobile-menu-list {
    display: table;
    height: 100%;
    width: 100%;

    > .inner {
      display: table-cell;
      vertical-align: middle;
    }

    .link {
      a {
        font-weight: 700;
        font-size: 24px;
        color: #222629;
        letter-spacing: 0;
        line-height: 56px;
        text-decoration: none;
      }
    }
  }
}

.mobile-popular {
  position: fixed;
  z-index: 102;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  box-sizing: border-box;
  padding: 0 24px;
  text-align: center;

  > .title {
    font-family: 'PT Mono', monospace;
    font-weight: 700;
    font-size: 24px;
    letter-spacing: 0;
    line-height: 40px;
    padding: 6px 0 0;

    &:after {
      content: "";
      margin: 9px auto 0;
      display: block;
      width: 48px;
      height: 3px;
      background: $red;
    }
  }
}

.popular-list {
  margin-top: 64px;

  .popular-item {
    margin-top: 32px;

    &:first-child {
      margin-top: 0;
    }
  }
}

.popular-item {
  display: block;
  text-decoration: none;

  > .title {
    display: block;
    font-weight: 700;
    font-size: 15px;
    color: #272727;
    letter-spacing: 0.53px;
    line-height: 18px;

    &:first-letter {
      text-transform: uppercase;
    }
  }

  > .desc {
    display: block;
    opacity: 0.54;
    font-size: 12px;
    color: #272727;
    letter-spacing: 0;
    line-height: 16px;
    margin-top: 8px;
  }
}

.mobile-popup-close {
  position: absolute;
  display: inline-block;
  top: 1rem;
  right: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  background: url(../images/bold-cross.svg) center / 80% no-repeat;
}

.mobile-popup {
  display: none;
  opacity: 0;
  transition: all .6s ease;

  &.isActive {
    opacity: 1;
  }
}

.mobile-category-link {
  margin-top: 48px;
  text-align: center;
  padding-bottom: 47px;

  a {
    display: inline-block;
    font-weight: 700;
    font-size: 16px;
    color: #222629;
    letter-spacing: 0.57px;
    padding-bottom: 8px;
    border-bottom: 3px solid $red;
    text-decoration: none;
  }
}

.mobile-category-dragger {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 23px;

  .icon {
    position: absolute;
    bottom: 8px;
    height: 7px;
    width: 20px;
    left: 50%;
    margin-left: -10px;
    background: url(../images/icon_burger-mini.svg) center / contain no-repeat;
  }

  .text {
    position: absolute;
    bottom: 19px;
    left: 0;
    right: 0;
    font-size: 12px;
    color: rgba(0,0,0,0.32);
    letter-spacing: 0;
    line-height: 16px;
    text-align: center;
  }
}

.mobile-categories {
  position: fixed;
  z-index: 102;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  box-sizing: border-box;

  > .title {
    font-weight: 700;
    font-size: 24px;
    color: #222629;
    letter-spacing: 0;
    line-height: 56px;
    padding-left: 24px;
  }

  > .categories-list {
    border-top: 3px solid $red;
    border-bottom: 3px solid $red;
    padding-bottom: 16px;

    .category-description.-link {
      border: none;
      margin-top: 16px;

      .category-card {
        > .title {
          padding-left: 64px;
        }

        > .desc {
          margin-left: 64px;
        }
      }
    }
  }

  > .bottom-nav {
    padding: 8px 0;
    text-align: center;

    a {
      font-weight: 700;
      font-size: 14px;
      color: #222629;
      letter-spacing: 0.49px;
      text-decoration: none;
      @include redHover();
    }
  }
}

.category-select-list {
  position: absolute;
  z-index: 3;
  top: 100%;
  left: 0;
  right: 0;
  display: none;

  .category-description {
    border-bottom: 1px solid #E0E2E4;
  }
}

.map-legend {
  position: absolute;
  z-index: 2;
  left: 180px;
  bottom: 60px;
}

.legend-block {
  display: inline-block;
  vertical-align: middle;
  margin-left: 40px;
  line-height: 30px;
  
  &:first-child {
    margin-left: 0;
  }

  .icon {
    display: inline-block;
    vertical-align: middle;
    background: {
      size: contain;
      repeat: no-repeat;
      position: center;
    }

    &.-flag {
      width: 30px;
      height: 20px;
      background-image: url(../images/icon_flag.png);
    }

    &.-dot {
      width: 16px;
      height: 16px;
      box-sizing: border-box;
      border: 3px solid $red;
      border-radius: 16px;
    }
  }

  .description {
    opacity: 0.54;
    font-size: 12px;
    color: #272727;
    letter-spacing: 0;
    line-height: 16px;
    margin-left: 10px;
  }
}

.fixed-favorites {
  transform: translateY(-5rem);
  z-index: 3;
  transition: all .6s ease;

  &.isActive {
    transform: translateY(0);
  }
}