html.isLocked {
  overflow-x: hidden;
  
  body {
    position: relative;
    overflow-x: hidden;
  }
}

html {
  &.isNotLoaded {
    opacity: 0;
  }
}

body {
  @media #{$mobile} {
    background: #f8f8f8;
  }
}

.ms {
  @media #{$desktop} {
    display: none !important;
  }
}

.ds {
  @media #{$mobile} {
    display: none !important;
  }
}

.section {
  &.-mfs {
    @media #{$mobile} {
      position: relative;
      z-index: 2;
      bottom: 0;
      left: 0;
      right: 0;
      height: 100vh;
      background: #fff;
      // transform: translateY(100%);
      // overflow: scroll;
      -webkit-overflow-scrolling: touch;
    }
  }

  @media #{$mobile} {
    transition: all .6s ease;

    &.isLocked {
      height: 0;
      overflow: hidden;
    }
  }
}

.share-link {
  position: relative;
  display: inline-block;
  width: 56px;
  height: 56px;
  border-radius: 56px;
  cursor: pointer;

  &:before {
    content: "";
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 56px;
    background: {
      color: $red;
      image: url(../images/icon-share.svg);
      position: center;
      repeat: no-repeat;
      size: 33%;
    }
    box-shadow: 0 1px 3px 0 rgba(207,23,28,0.35), 0 3px 15px 0 rgba(236,29,35,0.34);
    transition: all .5s ease;
  }

  &:after {
    content: "";
    position: absolute;
    top: 8px;
    left: 8px;
    right: 8px;
    bottom: 8px;
    border-radius: 56px;
    z-index: 3;
    opacity: 0;
    background: #eee url(../images/icon-red_cross.svg) center / 35% no-repeat;
    transition: all .5s ease;
  }

  .share-btn {
    position: absolute;
    width: 40px;
    height: 40px;
    top: 8px;
    right: 8px;
    opacity: 0;
    transition: all .4s ease;

    &.-tw {
      transition-delay: 0s;
    }

    &.-fb {
      transition-delay: .05s;
    }

    &.-vk {
      transition-delay: .1s;
    }

    svg {
      width: 100%;
    }
  }

  &.isActive {
    &:before {
      opacity: 0;
      transform: scale(0.75);
    }

    &:after {
      opacity: 1;
    }

    .share-btn {
      opacity: 1;

      &.-tw {
        top: 80px;
        right: 8px;
        transition-delay: .1s;
      }

      &.-fb {
        top: 100%;
        right: 100%;
        transition-delay: .05s;
      }

      &.-vk {
        right: 80px;
        top: 8px;
        transition-delay: 0s;
      }
    }
  }
}

.fixed-share {
  position: fixed;
  z-index: 101;
  bottom: 48px;
  right: 48px;
  transition: all .6s ease;
  // transform: translateY(120px);

  @media #{$mobile} {
    top: auto;
    bottom: 19px;
    right: 16px;
    z-index: 99;
    // transform: translateY(110px);
  }

  &.isActive {
    transform: translateY(0);
  }

  .share-link {
    &.isActive {
      .share-btn {
        opacity: 1;

        &.-tw {
          top: -64px;
          right: 8px;
        }

        &.-fb {
          top: -40px;
          right: 100%;
        }

        &.-vk {
          right: 80px;
          top: 8px;
        }
      }
    }
  }
}

.right-nav {
  display: table;
  position: absolute;
  z-index: 5;
  top: 0;
  right: 0;
  height: 100%;
  transition: all .4s cubic-bezier(0.165, 0.84, 0.44, 1);
  transform: translateX(100%);

  @media #{$mobile} {
    display: none;
  }

  &.isActive {
    transform: translateX(0);
  }

  > .inner {
    display: table-cell;
    vertical-align: middle;
  }

  .link {
    position: relative;
    display: block;
    height: 72px;
    width: 40px;
    margin-top: 24px;
    text-decoration: none;
    color: inherit;
    transform: translateX(32px);
    transition: all .4s cubic-bezier(0.165, 0.84, 0.44, 1);

    &:first-child {
      margin-top: 0;
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: #272727;
      transition: background .4s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    .text {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 72px;
      padding-bottom: 8px;
      display: inline-block;
      transform: rotate(-90deg);
      transform-origin: 0 100%;
      font-weight: 700;
      font-size: 16px;
      color: #272727;
      letter-spacing: 0.57px;
      text-align: center;
    }

    &.isActive {
      &:before {
        background: $red;
      }
    }

    &:hover,
    &.isActive {
      transform: translateX(0);
    }
  }
}

.mobile-down-link {
  display: none;
  position: absolute;
  z-index: 1;
  bottom: 16px;
  left: 16px;

  .text {
    font-weight: 700;
    font-size: 14px;
    color: #272727;
    letter-spacing: 0.49px;

    @media #{$mobile} {
      position: absolute;
      left: 28px;
      bottom: 0;
      line-height: 24px;
    }
  }

  .icon {
    display: block;
    width: 24px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    margin-top: 4px;

    svg {
      width: 16px;
    }
  }
}