.section.-advice {
	overflow: hidden;
	height: auto !important;

	@media #{$mobile} {
		background: #F8F8F8;
		overflow: visible;
	}

	.section-circle {
		content: "";
		position: absolute;
		pointer-events: none;
		left: -21px;
		bottom: -90px;
		height: 261px;
		width: 261px;
		background: url(../images/red-circle.svg) center / contain no-repeat;

		@media #{$mobile} {
			height: 396px;
			width: 396px;
			left: -212px;
			bottom: -57px;
		}
	}
}

.advice-slider {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 396px;
	padding-left: 68px;
	padding-right: 192px;
	display: flex;
	align-items: center;
	background: #F8F8F8;

	@media #{$mobile} {
		position: relative;
		left: 0;
		right: 0;
		padding: 0;
		top: 0;
		bottom: 0;
		background: transparent;
		overflow: hidden;

		.slick-list {
			overflow: visible;
		}
	}


	.slider-circle {
		content: "";
		position: absolute;
		pointer-events: none;
		top: -322px;
		left: -48px;
		height: 502px;
		width: 502px;
		background: url(../images/red-circle.svg) center / contain no-repeat;

		@media #{$mobile} {
			display: none;
		}
	}

	> .inner {
		position: relative;
		width: 100%;

		@media #{$mobile} {
			height: auto;
			padding: 110px 0 48px;
		}
	}

	.slides {
		margin: 152px 69px 0 117px;

		@media #{$mobile} {
			position: relative;
			margin: 0 18px;
			bottom: 0;
			top: 0;
		}

		.slick-arrow {
			position: absolute;
			top: 0;
			padding: 0;
			border: none;
			outline: none;
			background: transparent;
			transition: opacity .4s ease;

			&.slick-disabled {
				opacity: 0;
			}

			@media #{$mobile} {
				width: 26px;
				height: 42px;
				top: -3.5rem;

				svg {
					width: 100%;
					height: 100%;

					polygon {
						fill: #E3E3E3;
					}
				}
			}

			&.slick-prev {
				left: -117px;

				@media #{$mobile} {
					left: -10px;
				}
			}

			&.slick-next {
				right: -69px;

				@media #{$mobile} {
					right: -10px;
				}
			}

			svg {
				polygon {
					transition: all .4s ease;
				}
			}

			&:hover {
				svg {
					polygon {
						fill: $red;
					}
				}
			}
		}
	}
}

.advice-count {
	position: absolute;
	top: 0;
	right: 0;
	width: 28rem;

	@media #{$mobile} {
		width: 87px;
		top: 50px;
		height: 51px;
		right: 50%;
		margin-right: -43.5px;
	}

	.current {
		position: absolute;
		top: 32px;
		left: 68px;
		font-family: 'PT Mono', monospace;
		font-weight: 700;
		font-size: 180px;
		color: #E3E3E3;
		line-height: 196px;
		cursor: pointer;
		@include redHover();

		@media #{$mobile} {
			font-size: 40px;
			left: 0;
			top: auto;
			bottom: 0;
			line-height: 1;
		}
	}

	.slash {
		position: absolute;
		left: 237px;
		top: 50px;
		width: 125px;
		height: 11px;
		background: #E3E3E3;
		transform: rotate(45deg);

		@media #{$mobile} {
			width: 34px;
			height: 3px;
			top: 16px;
			right: 13px;
			left: auto;
		}
	}

	.amount {
		position: absolute;
		top: 0;
		left: 320px;
		font-family: 'PT Mono', monospace;
		font-weight: 700;
		font-size: 64px;
		color: #E3E3E3;
		letter-spacing: 0;
		line-height: 56px;

		@media #{$mobile} {
			font-size: 20px;
			line-height: 1.2;
			top: 0;
			right: 0;
			left: auto;
		}
	}
}

.advice-slide {
	outline: none !important;

	@media #{$mobile} {
		overflow: auto;
		height: 100%;

		> .inner {
			height: 100%;
			padding: 0 6px;
			box-shadow: 0 3px 15px 0 rgba(96,110,145,0.14);
			box-sizing: border-box;

			.inner-content {
				padding: 24px 16px;
				background: #fff;
			}
		}
	}
	
	.title {
		position: relative;
		font-weight: 700;
		font-size: 40px;
		line-height: 32px;
		padding-left: 65px;

		@media #{$mobile} {
			padding: 0;
			font-weight: 700;
			font-size: 20px;
			color: #272727;
			letter-spacing: 0;
			line-height: 24px;
		}

		.icon {
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -24px;
			width: 48px;
			height: 48px;

			@media #{$mobile} {
				display: none;
			}

			&.-n1 {
				background: url(../images/advice/1.svg) center / contain no-repeat;
			}

			&.-n2 {
				background: url(../images/advice/2.svg) center / contain no-repeat;
			}

			&.-n3 {
				background: url(../images/advice/3.svg) center / contain no-repeat;
			}

			&.-n4 {
				background: url(../images/advice/4.svg) center / contain no-repeat;
			}

			&.-n5 {
				background: url(../images/advice/5.svg) center / contain no-repeat;
			}

			&.-n6 {
				background: url(../images/advice/6.svg) center / contain no-repeat;
			}

			&.-n7 {
				background: url(../images/advice/7.svg) center / contain no-repeat;
			}

			&.-n8 {
				background: url(../images/advice/8.svg) center / contain no-repeat;
			}

			&.-n9 {
				background: url(../images/advice/9.svg) center / contain no-repeat;
			}

			&.-n10 {
				background: url(../images/advice/10.svg) center / contain no-repeat;
			}
		}
	}

	.desc {
		margin-top: 24px;
		font-size: 16px;
		letter-spacing: 0;
		line-height: 24px;
		overflow: hidden;

		@media #{$mobile} {
			margin-top: 8px;
			font-size: 14px;
			color: #272727;
			letter-spacing: 0;
			line-height: 21px;
		}

		.column {
			float: left;
			width: 50%;
			box-sizing: border-box;
			opacity: 0;
			transition: opacity 1s ease;

			@media #{$mobile} {
				width: auto;
				float: none;
				padding: 0 !important;
			}

			&:first-child {
				padding-right: 20px;
			}

			&:last-child {
				padding-left: 20px;

				@media #{$mobile} {
					margin-top: 1em;
				}
			}
		}
	}
}

.advice-slide.slick-active {
	.desc {
		.column {
			opacity: 1;
			transition: opacity 1s ease .5s;
		}
	}
}

.advice-description {
	position: absolute;
	top: 120px;
	left: 116px;
	width: 232px;
	bottom: 0;

	.kagocel-link {
		position: absolute;
		bottom: 48px;
	}

	.preview {
		font-size: 20px;
		letter-spacing: 0;
		line-height: 24px;
		margin-top: 16px;
	}

	.description {
		opacity: 0.54;
		font-size: 14px;
		letter-spacing: 0;
		line-height: 21px;
		margin-top: 16px;
	}
}

.section-title {
	font-family: 'PT Mono', monospace;
	font-weight: 700;
	font-size: 64px;
	color: #222629;
	letter-spacing: 0;
	line-height: 56px;
}

.slick-track,
.slick-list {
	@media #{$mobile} {
		height: 100%;
	}
}