.section.-collections {
	@media #{$mobile} {
		background: transparent;
	}
}

.collections {
	position: absolute;
	right: 165px;
	top: 120px;
	left: 165px;
	bottom: 45px;
	padding: 60px 0;
	overflow: hidden;

	@media #{$mobile} {
		top: 57px;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 35px 0 0;
	}

	.section-title {
		position: absolute;
		top: 0;
	}

	.kagocel-link {
		position: absolute;
		bottom: 0;
		left: 0;
	}

	.collections-list {
		display: flex;
		padding: 64px 0 64px 47px;
		box-sizing: border-box;
		height: 100%;
		align-items: center;

		@media #{$mobile} {
			padding: 0;
			display: block;
		}

		.inner {
			display: flex;

			@media #{$mobile} {
				display: block;
				width: 80%;
				margin: 0 auto;

				.slick-list {
					overflow: visible;
				}
			}
		}

		.collection {
			float: left;
			position: relative;
			padding: 0 20px 45px;
			width: 25%;
			text-decoration: none;
			color: inherit;

			@media #{$mobile} {
				width: 70vw;
				padding: 0 12px 45px;
			}

			.image-wrap {
				display: block;
				position: relative;

				&:before {
					content: "";
					position: absolute;
					z-index: 2;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 95%;
					height: 33%;
					background: $red;
					transition: width .4s ease;
				}

				&:after {
					content: "";
					position: absolute;
					z-index: 1;
					top: 11px;
					left: 13px;
					right: 13px;
					bottom: 0;
					box-shadow: 0 10px 20px rgba(182, 188, 191, .50);
				}
			}

			.image {
				position: relative;
				z-index: 3;
				display: block;
				background: {
					size: cover;
					position: center;
				}
			}

			.title {
				display: block;
				font-weight: 700;
				font-size: 20px;
				color: #272727;
				letter-spacing: 0;
				line-height: 24px;
				margin-top: 24px;
			}

			.desc {
				display: block;
				opacity: 0.54;
				font-size: 14px;
				line-height: 21px;
				margin-top: 8px;
			}

			.bottom {
				display: block;
				position: absolute;
				bottom: 0;
				left: 20px;

				@media #{$mobile} {
					left: 12px;
				}

				span {
					font-size: 12px;
					color: #D6D6D6;
					letter-spacing: 0.11px;
					line-height: 14px;
					text-decoration: none;
					transition: color .4s ease;
				}
			}

			&:hover {
				.image-wrap {
					&:before {
						width: 120%;
					}
				}

				.bottom {
					span {
						color: $red;
					}
				}
			}
		}
	}
}