.section.-main {
  position: relative;
  z-index: 1;
  background: #fff;

  .screen-background {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: {
      position: center;
      size: 155%;
      repeat: no-repeat;
    }

    @media #{$mobile} {
      background: {
        position: top -4vh left 27.5%;
        size: auto 90vh;
        repeat: no-repeat;
      }
    }

    &.-n1 {
      background-image: url(../images/background-main.png);
    }

    &.-n2 {
      background-image: url(../images/background-city.jpg);
    }
  }

  .screen-message {
    position: absolute;
    z-index: 2;
    top: 0;
    bottom: 0;
    right: 50%;
    left: 0;
    margin-right: 50px;
    pointer-events: none;
    background: {
      image: url(../images/winter_rest-words.svg);
      repeat: no-repeat;
      position: right center;
      size: 56% auto;
    }

    @media #{$mobile} {
      left: 0;
      right: 0;
      top: 0;
      height: 75vh;
      margin-right: 0;
      background: {
        image: url(../images/winter_rest-words_mobile.svg);
        position: right 6vw top -1vh;
        size: contain;
      }
    }
  }

  .share-link {
    position: absolute;
    right: 48px;
    top: 48px;

    @media #{$mobile} {
      display: none;
    }
  }
}

.main-content {
  margin-left: 50%;
  padding-left: 90px;
  height: 100%;

  @media #{$mobile} {
    height: auto;
    padding: 83vh 24px 35px;
    margin: 0;
  }

  > .inner {
    position: relative;
    display: table;
    height: 100%;

    @media #{$mobile} {
      display: block;
      height: auto;
    }

    > .main-text {
      display: table-cell;
      vertical-align: middle;

      @media #{$mobile} {
        display: block;
      }
    }
  }
}

.main-text {
  top: 5.1%;
  position: relative;

  > .title {
    font-weight: 700;
    font-size: 40px;
    color: #272727;
    letter-spacing: 0;
    line-height: 32px;

    @media #{$mobile} {
      font-size: 26px;
      line-height: 30px;
      white-space: nowrap;
    }

    @media screen and (max-width: 340px) {
      font-size: 25px;
    }
  }

  > .desc {
    margin-top: 32px;
    font-size: 16px;
    color: #272727;
    letter-spacing: 0px;
    line-height: 24px;

    @media #{$mobile} {
      font-size: 14px;
      line-height: 21px;
      margin-top: 22px;
    }
  }
}

.main-down-link {
  display: inline-block;
  position: absolute;
  bottom: 48px;
  left: 0;
  text-decoration: none;
  color: inherit;
  @include redHover();
  transition: color .4s ease;

  @media #{$mobile} {
    position: static;
    margin-top: 48px;
    width: 100%;
    color: $red;
  }

  > .text {
    font-weight: 700;
    font-size: 16px;
    letter-spacing: 0.57px;
    margin: 0 auto;

    @media #{$mobile} {
      line-height: 18px;
      width: 125px;
      text-align: center;
    }
  }

  > .icon {
    width: 25px;
    height: 15px;
    margin-top: 24px;

    @media #{$mobile} {
      margin: 18px auto auto;
    }

    svg {
      width: 100%;

      polygon {
        transition: all .4s ease;

        @media #{$mobile} {
          fill: $red;
        }
      }
    }
  }

  &:hover {
    > .icon {
      svg {
        polygon {
          fill: $red;
        }
      }
    }
  }
}

.kagocel-link {
  display: inline-block;
  width: 145px;
  height: 41px;
  background: url(../images/logo.png) center right / contain no-repeat;

  &.-right {
    background: url(../images/logo.png) center right / contain no-repeat;
    width: 145px;
    height: 41px;
  }
}

.fixed-logo {
  position: fixed;
  z-index: 98;
  right: 35px;
  top: 3rem;
  // transform: translateY(-100px);
  transition: all .4s ease;

  @media #{$mobile} {
    top: auto;
    right: auto;
    left: 1rem;
    bottom: 35px;
  }

  .main-kagocel {
    right: 0;
    top: 0;

    @media #{$mobile} {
      right: auto;
      top: auto;
      left: 0;
      bottom: 0;
      width: 100px;
      height: 30px;
    }
  }

  &.isActive {
    transform: translateY(0);
  }
}

.main-kagocel {
  position: absolute;
  bottom: 48px;
  right: 35px;
  @extend .kagocel-link;

  &.main-logo {
    bottom: auto;
    top: 48px;

    @media #{$mobile} {
      top: 24px;
    }
  }

  @media #{$mobile} {
    bottom: auto;
    right: auto;
    width: 165px;
    height: 48px;
    left: 24px;
    top: 24px;
    // background: url(../images/logo-main_bottom_right.svg) center left / contain no-repeat;
  }
}

.kagocel-info {
  position: absolute;
  display: inline-block;
  margin-top: 16px;
  bottom: 48px;
  left: 48px;
  white-space: nowrap;
  opacity: 0.54;
  font-size: 12px;
  color: #272727;
  letter-spacing: 0;
  line-height: 16px;
}